//引用mSearch组件，如不需要删除即可
import mSearch from '@/components/mehaotian-search-revision/mehaotian-search-revision.vue';
import {
	dateFormat
} from '@/utils/index'
export default {
	components: {
		mSearch
	},
	data() {
		return {
			popErrorMsg: '',
			placeholder: '请输入完整的单号或初评编号',
			address: '',
			inputSearch: '',
			recordList: [],
		}
	},
	onLoad: function(option) { //option为object类型，会序列化上个页面传递的参数
		this.inputSearch = option.keycode;
		this.doSearch();
	},
	mounted() {
		/* this.initData(); */
	},
	onShow() {
		console.log("出现");
	},
	methods: {
		ohShitfadeOut() {
			var fadeOutTimeout = setTimeout(() => {
				this.popErrorMsg = '';
				clearTimeout(fadeOutTimeout);
			}, 1000);
		},
		doSearch() {
			//this.inputSearch = e;
			if (this.inputSearch.length - 7 >= 0) {
				this.initData();
			} else {
				if (this.inputSearch != '') {
					this.popErrorMsg = '请输入完整的单号或初评编号';
					this.ohShitfadeOut();
				}
			}
		},
		async initData() {
			// 显示顶部刷新图标
			wx.showNavigationBarLoading();
			console.log("下拉刷新")
			var that = this;
			let url = "webService/getZtsmList";
			let data = {
				keyword: this.inputSearch
			};
			let resp = await that.$post1(url, data);
			console.log(333, resp.data);
			this.recordList = resp.data;
			if(this.recordList == null || this.recordList == undefined  || this.recordList.length <= 0){
				that.address = '';
				uni.showToast({
					icon:'none',
					title: '暂时无法查询或该单号不存在',
					duration: 3000
				});
				// 隐藏导航栏加载框
				wx.hideNavigationBarLoading();
				return;
			}
			this.recordList.forEach((item, index) => {
				that.address = item.address
				item.tjsjDatetime = dateFormat(new Date(item.tjsjDatetime), 'MM-dd')
			})

			// 隐藏导航栏加载框
			wx.hideNavigationBarLoading();
		}
	},
}
